<div class="select-game"><label class="select-game--label" for="load-game-informations">Select a game </label><select class="select-game--select" id="load-game-informations" name="game-infos" v-model="selectedGameInfos"><option v-for="(gameFilename, index) in listGameFilename" :value="gameFilename">{{ gameFilename.name }}</option></select><button class="btn btn--primary btn--load" type="button" @click="load">Load</button></div>