<div class="info-card" :draggable="isDraggable" @dragstart="handleDragStart" @dragend="handleDragEnd"><div class="info-card--text" v-if="isText"><p v-for="text in infoText">{{ text }}</p></div><AudioPlayer v-else-if="isAudio" :music-url="info.value" :card-id="info.id"></AudioPlayer><div class="info-card--text" v-else>x</div></div>