import YAML from 'yaml';

function loadYamlFile(filepath) {
  return fetch(filepath)
    .then(res => {
      if (res.ok) {
        return res.text();
      }
      throw new Error(`file not found: ${filepath}`);
    })
    .then(data => {
      return YAML.parse(data);
    });
}

function loadListGameFilename() {
  return loadYamlFile('game-informations/list-th-game.yaml');
}

function loadGameInformations(filename) {
  return loadYamlFile(`game-informations/${filename}`);
}

export {
  loadListGameFilename,
  loadGameInformations,
};