<div class="video-container"><Youtube v-if="hasActiveVideo" :src="musicUrl" :style="youtubeStyle" width="70" height="50" ref="youtube" @ready="youtubeReady" @state-change="youtubeStateChange"></Youtube><div class="play-button play-button--disabled" v-if="hasDisabledButton"><div class="play-button__center-icon"><div class="button-icon loading-icon"></div></div></div><div class="play-button" v-else-if="hasSingleButton" @click="actionButton"><div class="play-button__center-icon"><div class="button-icon" :class="state.iconName"></div></div></div><div class="play-button" v-else @click="actionButton"><div class="play-button__left-icon"><div class="button-icon" :class="state.iconName"></div></div><div class="play-button__right-icon" @click="resetVideo"><div class="button-icon reset-icon"></div></div></div><div class="card-id">{{ cardId }}</div></div>