const URL_PATTERNS = [
  /^https:\/\/www\.youtube\.com\/watch\?v=.{11}$/
];

const CHARACTERS_INFO_CARD = [
  {
    type: 'text',
    name: 'name',
    dataName: 'characterName',
    hasValidData() {
      return true;
    },
    valueFromData(data) {
      return data.name;
    },
  },
  {
    type: 'text',
    name: 'species',
    dataName: 'characterSpecies',
    hasValidData() {
      return true;
    },
    valueFromData(data) {
      return data.species;
    },
  },
  {
    type: 'text',
    name: 'abilities',
    dataName: 'characterAbilities',
    hasValidData() {
      return true;
    },
    valueFromData(data) {
      return data.abilities;
    },
  },
];

const MUSICS_INFO_CARD = [
  {
    type: 'text',
    name: 'name',
    dataName: 'musicName',
    hasValidData() {
      return true;
    },
    valueFromData(data) {
      return data.name;
    },
  },
  {
    type: 'audio',
    name: 'url',
    dataName: 'musicUrl',
    cellName: 'urlCell',
    hasValidData(data) {
      return URL_PATTERNS.some(regex => regex.test(data.url));
    },
    valueFromData(data) {
      return data.url;
    },
  },
];

const CHARACTER_CELLS = CHARACTERS_INFO_CARD.map(({dataName}) => dataName);
const MUSIC_CELLS = MUSICS_INFO_CARD.map(({dataName}) => dataName);

export {
  CHARACTERS_INFO_CARD,
  MUSICS_INFO_CARD,
  CHARACTER_CELLS,
  MUSIC_CELLS,
};
