let tableGuessContainer = null;
let dragStartContainer = null;

function setTableGuessContainer(c) {
  tableGuessContainer = c;
}

function setDragStartContainer(c) {
  dragStartContainer = c;
}

function setDraggedDataName(dataName) {
  tableGuessContainer.setDraggedDataName(dataName);
}

function switchInfoCard(startInfoCard, endInfoCard, dragEndContainer) {
  // Switch info cards between both containers
  // and then reset dragStartContainer
  dragStartContainer.removeInfoCard(startInfoCard);
  dragStartContainer.setInfoCard(endInfoCard);
  dragEndContainer.setInfoCard(startInfoCard);
  dragStartContainer = null;
}

export {
  setTableGuessContainer,
  setDragStartContainer,
  setDraggedDataName,
  switchInfoCard,
};
