import {createApp} from 'vue';
import App from './App';

import '@/assets/reset.styl';
import '@/assets/button.styl';

import {loadListGameFilename} from '@/utils/yamlLoader';

loadListGameFilename()
  .then(listGameFilename => {
    return {listGameFilename};
  })
  .then(props => {
    createApp(App, props)
      .mount('#app');
  });
